@import url(https://fonts.googleapis.com/css?family=Leckerli+One);
:root {
  --off-white: #fbfbfb;
  --light-grey: #a5a6a9;
  --creme: #e5e7de;
  --grey: #dddddd;
  --dark-grey: #0b3536;
  --orange: #f67944;
  --blue: #0098d8;
  --green: #3dbd5d;
  --red: #f54123;
  --canvas-width: 250px;
  --container-max-width: 640px;
  --content-padding: 1.2em;
  --shadow: -1px -1px 0 #000003, 1px -1px 0 #000003, -1px 1px 0 #000003,
    1px 1px 0 #000003;
  --light-shadow: -1px -1px 0 #fbfbfb, 1px -1px 0 #fbfbfb, -1px 1px 0 #fbfbfb,
    1px 1px 0 #fbfbfb;
}

html,
body {
  height: 100%;
}

body {
  background: -webkit-radial-gradient(black 15%, transparent 16%) 0 0,
    -webkit-radial-gradient(black 15%, transparent 16%) 8px 8px,
    -webkit-radial-gradient(rgba(255, 255, 255, 0.1) 15%, transparent 20%) 0 1px,
    -webkit-radial-gradient(rgba(255, 255, 255, 0.1) 15%, transparent 20%) 8px 9px;
  background: radial-gradient(black 15%, transparent 16%) 0 0,
    radial-gradient(black 15%, transparent 16%) 8px 8px,
    radial-gradient(rgba(255, 255, 255, 0.1) 15%, transparent 20%) 0 1px,
    radial-gradient(rgba(255, 255, 255, 0.1) 15%, transparent 20%) 8px 9px;
  background-color: #282828;
  background-size: 16px 16px;
  color: #fbfbfb;
  color: var(--off-white);
  font-family: Helvetica, sans-serif;
  font-size: 1em;
  font-weight: 300;
  line-height: 1.5;
  display: flex;
  flex-direction: column;
}

h1,
h2,
h3,
button {
  color: #f1f2f0;
  text-shadow: -1px -1px 0 #000003, 1px -1px 0 #000003, -1px 1px 0 #000003,
    1px 1px 0 #000003;
  text-shadow: var(--shadow);
}

h1 {
  letter-spacing: 1px;
}

h2 {
  letter-spacing: 2px;
}

h2,
h3 {
  font-weight: 300;
}

h3 {
  letter-spacing: 3px;
}

.content {
  max-width: 640px;
  max-width: var(--container-max-width);
  margin: 0 auto;
  padding: 1.2em;
  padding: var(--content-padding);
}

main {
  flex: 1 0 auto;
}

.header,
.footer {
  width: 100%;
}

.header {
  background-color: #3dbd5d;
  background-color: var(--green);
}

.header .brand {
  color: #0b3536;
  color: var(--dark-grey);
  text-shadow: -1px -1px 0 #fbfbfb, 1px -1px 0 #fbfbfb, -1px 1px 0 #fbfbfb,
    1px 1px 0 #fbfbfb;
  text-shadow: var(--light-shadow);
}

.header h1 {
  color: #fbfbfb;
  color: var(--off-white);
}

.footer {
  color: #0b3536;
  color: var(--dark-grey);
  flex-shrink: 0;
  background-color: #dddddd;
  background-color: var(--grey);
}

.header .content,
.footer .content {
  max-width: 640px;
  max-width: var(--container-max-width);
  margin: 0 auto;
  padding: 1.2em;
  padding: var(--content-padding);
}

.header .content {
  padding-bottom: 0;
}

.footer .content {
  position: relative;
}

pre {
  overflow: hidden;
}

.object-link:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

button {
  background: #e5e7de;
  background: var(--creme);
  border: none;
  box-shadow: -1px -1px 0 #000003, 1px -1px 0 #000003, -1px 1px 0 #000003,
    1px 1px 0 #000003;
  box-shadow: var(--shadow);
  cursor: pointer;
  font-size: 1rem;
  letter-spacing: 3px;
  padding: 1rem 2rem;
  text-decoration: none;
  text-align: center;
  -webkit-transition: background 250ms ease-in-out, color 250ms ease-in-out,
    -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, color 250ms ease-in-out,
    -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, color 250ms ease-in-out,
    transform 150ms ease;
  transition: background 250ms ease-in-out, color 250ms ease-in-out,
    transform 150ms ease, -webkit-transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

button:hover,
button:focus {
  color: #0098d8;
  color: var(--blue);
  background: #a5a6a9;
  background: var(--light-grey);
}

button:focus {
  outline: 1px solid #e5e7de;
  outline: 1px solid var(--creme);
  outline-offset: -4px;
}

button:active {
  -webkit-transform: scale(0.99);
          transform: scale(0.99);
}

.demo {
  max-width: calc(640px + 2.5em);
  max-width: calc(var(--container-max-width) + 2.5em);
  margin: 0 auto;
}

@media only screen and (max-width: 640px) {
  .wrap {
    flex-wrap: wrap;
  }

  .wrap-reverse {
    flex-wrap: wrap-reverse;
  }

  .product-container > canvas {
    padding: 0 1.2em 1.2em;
    padding: 0 var(--content-padding) var(--content-padding);
  }
}

.demo {
  background: var(--off-white);
  padding: 0.8em;
  text-align: center;
}

.demo h1 {
  background: var(--light-grey);
  color: var(--orange);
  font-family: "Leckerli One", cursive;
  font-size: 2.4em;
  margin: 0.25em 0;
  padding-top: 0.25em;
  padding-bottom: 0.25em;
}

.score {
  background-color: #dbb700;
  display: flex;
  justify-content: space-around;
  margin: 0.25em 0;
}

/* Round */

.flex-wrapper {
  box-shadow: var(--shadow);
  display: flex;
  flex-wrap: nowrap;
  margin: 0.75em auto;
  position: relative;
  width: 100%;
}

.canvas-wrapper,
.question,
.controls {
  flex-basis: calc(100% / 3);
}

.canvas-wrapper {
  padding-top: calc(100% / 3); /* 1:1 Aspect Ratio */
  position: relative; /* If you want text inside of it */
}

canvas {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
}

.question {
  align-items: center;
  background-color: var(--creme);
  display: flex;
  font-family: "Leckerli One", cursive;
  justify-content: left;
}

.question > span {
  align-items: center;
  border: solid 1px var(--red);
  box-shadow: var(--shadow);
  display: flex;
  height: 50px;
  justify-content: center;
  margin: 1em;
  width: 50px;
}

.question span {
  color: var(--red);
}

.response-wrapper {
  display: block;
  height: 320px;
  position: relative;
}

.response h1 {
  background: none;
}

.response .is-cursive {
  font-family: "Leckerli One", cursive;
  font-size: 2.2rem;
  vertical-align: sub;
}

.response-wrapper button {
  background-color: var(--dark-grey);
  box-shadow: none;
}

.response-wrapper button:hover {
  color: var(--blue);
}

.response-wrapper button span {
  font-size: 64px;
}

.is-green {
  color: var(--green);
}

.is-orange {
  color: var(--orange);
}

.response {
  align-items: center;
  background-color: var(--dark-grey);
  color: var(--creme);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  position: absolute;
  width: 100%;
  height: 100%;
}

.response > div {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.btn-no {
  background-color: var(--orange);
}

.btn-yes {
  background-color: var(--green);
}

.controls {
  align-items: center;
  background-color: var(--blue);
  display: flex;
  flex-direction: column;
  grid-area: controls;
  justify-content: space-evenly;
  order: 2;
}

.controls button {
  margin: 1em;
}

